<template>
  <page current>
    <div class="gap-sides mt-10">
        <ui-button @click="onShowSheet">ActionSheet</ui-button>
        <ui-button type="success" @click="onActionSheet">ActionSheet</ui-button>
        <ui-button type="success" @click="onActionSheetb">ActionSheet2</ui-button>
    </div>
    <ui-actionsheet 
     v-model="showActionSheet"
     :maskClose="true"
     title=""
     subtitle=""
     :menus="actionMenus"
     @on-mask="test"></ui-actionsheet>
  </page>
</template>

<script>
  import Page from '../components/page'
  import {UiButton} from '../components/buttons'
  import {UiActionsheet} from '../components/actionsheet'
  
  export default {
    name: 'actionsheet',
    components: {
      Page,
      UiButton,
      UiActionsheet
    },
    data () {
      return {
        showActionSheet: false,
        actionMenus: [
          {
            text: "",
            className: 'text-success',
            onClick(){
              console.log("");
            }
          },
          {
            text: "",
            onClick(){
              console.log("");
            }
          }
        ]
      }
    },
    methods:{
      test(){
        console.log("mask");
      },
      onShowSheet(){
        this.showActionSheet = true
      },
      onActionSheet(){
        this.$actionsheet({
          maskClose: true,
          onMask(){
            console.log("click mask")
          },
          menus: [
            {
              text: "",
              onClick(){
                alert("")
              }
            },
            {
              text: "",
              onClick(){
                alert("")
              }
            }
          ]
        })
      },
      onActionSheetb(){
        this.$actionsheet({
          maskClose: true,
          onMask(){
            console.log("click mask")
          },
          menus: [
            {
              text: "",
              onClick(){
                alert("")
              }
            },
            {
              text: "",
              onClick(){
                alert("")
              }
            }
          ]
        })
      }
    }
  }
</script>


