<template>
    <page current>
        <div class="gap-sides mt-10">
            <ui-button>01</ui-button>
            <ui-button type="primary">primary</ui-button>
            <ui-button type="primary" disabled>primary</ui-button>
            <ui-button type="success">success</ui-button>
            <ui-button type="warn">warn</ui-button>
            <ui-button type="danger">danger</ui-button>
            <ui-button type="info">info</ui-button>
            <ui-button plain>01</ui-button>
            <ui-button type="primary" plain>plain-info</ui-button>
            <ui-button type="primary" plain disabled>plain-info</ui-button>
            
            <ui-button type="primary" size="sm" plain>sm-plain-info</ui-button>
        </div>
    </page>
</template>

<script>
  import Page from '../components/page'
  import {UiButton} from '../components/buttons'
  
  export default {
    name: 'button',
    components: {
      Page,
      UiButton
    }
  }
</script>


