<template>
  <page current>
    <ui-cells class="mt-10">
     <ui-popup-picker label="Picker" :data="pickDate" v-model="selected" @change="onChange" placeholder=""></ui-popup-picker>
     <ui-cell>{{selected}}</ui-cell>
     <ui-datetime-picker label="Datetime Picker" placeholder="" max="2019-12-31" v-model="selectedDatetime"></ui-datetime-picker>
     <ui-cell>{{selectedDatetime}}</ui-cell>
     <ui-city-picker label="City Picker" v-model="selectedCity"></ui-city-picker>
     <ui-cell>{{selectedCity}}</ui-cell>
    </ui-cells>
  </page>
</template>

<script>
  import Page from '../components/page'
  import {UiCells, UiCell} from '../components/cell'
  import {UiInput} from '../components/form'
  import {UiButton} from '../components/buttons'
  import {UiPopupPicker, UiCityPicker,UiDatetimePicker} from '../components/picker'
//<ui-popup-picker label="Picker" :data="pickDate" v-model="selected" @change="onChange" placeholder=""></ui-popup-picker>
    //  <ui-city-picker label="City Picker" v-model="selectedCity"></ui-city-picker>
    //  <ui-datetime-picker label="Datetime Picker" placeholder="" v-model="selectedDatetime"></ui-datetime-picker>
    //  <ui-cell>{{selectedCity}}</ui-cell>
  export default {
    name: 'picker',
    components: {
      Page,
      UiCells,
      UiCell,
      UiInput,
      UiButton,
      UiPopupPicker,
      UiCityPicker,
      UiDatetimePicker
    },
    data () {
      return {
        pickerState: false,
        selected: [],
        selectedCity: ['','',''],
        selectedDatetime: [],
        pickDate: [
          ['','','','','','',''],
          [{
            name: '',
            value: 'a'
          },{
            name: '',
            value: 'b'
          },{
            name: '',
            value: 'c'
          }]
          ]
      }
    },
    methods:{
      onChange(val){
        //console.log(this.selected);
      },
      showEvent(val){
        //console.log(this.selected);
      }
    }
  }
</script>


