<template>
  <page current>
    <div class="gap-sides mt-10">
        <ui-button @click="onAlert"></ui-button>
        <ui-button @click="onDialog"></ui-button>
        <ui-button @click="onAlertPlus" type="success"></ui-button>
        <ui-button @click="onDialogPlus" type="success"></ui-button>
    </div>

    <ui-dialog
    title="" 
    text=""
    v-model="showDialog"
    @on-confirm="onClick">
    </ui-dialog>
    
    <ui-dialog
    title="" 
    text=""
    type="alert"
    v-model="showAlert"
    @on-confirm="onClick">
    </ui-dialog>
  </page>
</template>

<script>
  import Page from '../components/page'
  import {UiButton} from '../components/buttons'
  import {UiDialog} from '../components/dialog'
  
  export default {
    name: 'toast',
    components: {
      Page,
      UiButton,
      UiDialog
    },
    data () {
      return {
        showAlert: false,
        showDialog: false
      }
    },
    methods:{
      onAlert(){
        this.showAlert = true;
        console.log("show Alert");
      },
      onDialog(){
        this.showDialog = true;
        console.log("show Dialog");
      },
      onClick(){
       console.log("")
      },
      onAlertPlus(){
        this.$alert('','', function(){
          console.log('')
        });
      },
      onDialogPlus(){
        this.$dialog('', function(){
          console.log('kkkkkkk');
        }, function(){
          console.log('laiaidas das ');
        });
      }
    }
  }
</script>


